"use client";
import { useTranslation } from "react-i18next";

//3rd Party
import {
  Accordion,
  AccordionContent,
  AccordionPanel,
  AccordionTitle,
} from "flowbite-react";
//Redux
import { offsetCount } from "@/redux/slices/userSlice";
import { useDispatch } from "react-redux";
//Component

export default function SearchProductFilter({
  categoryList,
  mPrices,
  anyPrice,
  setAnyPrice,
  customPrice,
  setCustomPrice,
  customLowPrice,
  setCustomLowPrice,
  customHighPrice,
  setCustomHighPrice,
  setOffset,
  childCategoryShow,
  handleParentCategoryClick,
  childCategoryList,
  handleChildCategoryBack,
  subChildCategoryList,
  subChildCategoryShow,
  handleChildCategoryClick,
  handleSubChildCategoryBack,
  handleSubChildCategoryClick,
  subChildCategoryslug,
  handleResetAll,
}) {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  
  const handleprice = (e, btnChk) => {
    if (btnChk == "radio1") {
      setAnyPrice(e.target.checked);
      setCustomPrice(false);
      setCustomLowPrice("");
      setCustomHighPrice("");
    } else {
      setAnyPrice(false);
      setCustomPrice(e.target.checked);
    }
  };

  const handleCustomLowInput = (e, inputChk) => {
    setCustomLowPrice(e.target.value);
    dispatch(offsetCount(0));
    // setOffset(0)
  };
  const handleCustomHighInput = (e) => {
    const inValue = Number(e.target.value);
    const maxVale = Number(mPrices);
    if (inValue <= maxVale) {
      setCustomHighPrice(e.target.value);
    } else {
      setCustomHighPrice(mPrices);
    }
    dispatch(offsetCount(0));
    // setOffset(0)
  };
  const hadleKeyInput = () => {
    setCustomPrice(true);
    setAnyPrice(false);
  };

  return (
    <div className="hidden md:block rtl:ml-4">
      <div className="bg-gray-100 px-4 py-[1.125rem] w-80 h-full shrink-0">
        <div className="sticky top-0 h-full">
          <div className="flex items-center justify-between mb-3">
            <h3 className="text-base uppercase text-black-300">
              {t("productlist.Filter")}
            </h3>
            <a
              role="button"
              onClick={() => handleResetAll()}
              className="flex items-center space-x-1.5 rtl:space-x-[unset] font-medium text-[#2874F0] text-xs cursor-pointer"
            >
              <p className="rtl:ml-1.5">{t("myprofile.ResetAll")}</p>
              <img src="img/close-blue.svg" alt="close-blue" />
            </a>
          </div>
          <Accordion className="flex flex-col mb-4 bg-white border-0 group drop-shadow">
            <AccordionPanel>
              <AccordionTitle className="px-4 py-3 pr-3 mb-0 text-sm font-medium bg-white border-0 border-b-0 rounded-md hover:bg-white focus:ring-0 text-black-300">
                {t("myprofile.categories")}
              </AccordionTitle>
              <AccordionContent className="px-2 py-4 pt-0 bg-white border-0 border-t-0 rounded-b-lg border-x-0">
                {/* parent category */}
                <div
                  className={
                    childCategoryShow
                      ? "hidden"
                      : " flex flex-col gap-0 items-start"
                  }
                >
                  {categoryList.map((category, index) => (
                    <a
                      role="button"
                      key={index}
                      onClick={(e) => handleParentCategoryClick(category)}
                      className="relative w-full text-sm font-normal cursor-pointer text-black-300 style-5"
                    >
                      {category.name}
                    </a>
                  ))}
                </div>

                {/* child category */}
                <div
                  className={
                    childCategoryShow
                      ? subChildCategoryShow
                        ? "hidden"
                        : "flex flex-col space-y-2 items-start"
                      : "hidden"
                  }
                >
                  <a
                    role="button"
                    className="flex items-center gap-[3px] px-2 font-normal text-black-300 text-sm capitalize cursor-pointer style-4"
                    onClick={(e) => handleChildCategoryBack()}
                  >
                    <img
                      src="img/arrow-right-gray.svg"
                      alt="arrow-right-gray"
                    />{" "}
                    {t("forgotpassword.Back")}
                  </a>
                  <h3 className="px-2 font-normal text-[#2874F0] text-sm">
                    {childCategoryList?.categoryNameTrans
                      ? childCategoryList?.categoryNameTrans
                      : childCategoryList?.name}
                  </h3>
                  <div className="flex flex-col items-start w-full px-4">
                    {childCategoryList?.children?.map(
                      (childCategory, index) => (
                        <a
                          role="button"
                          key={index}
                          onClick={(e) =>
                            handleChildCategoryClick(childCategory)
                          }
                          className="relative w-full text-sm font-normal cursor-pointer text-black-300 style-5 sub"
                        >
                          {childCategory?.categoryNameTrans
                            ? childCategory?.categoryNameTrans
                            : childCategory?.name}
                        </a>
                      )
                    )}
                  </div>
                </div>
                {/* sub child category */}
                <div
                  className={
                    subChildCategoryShow
                      ? "flex flex-col space-y-2 items-start"
                      : "hidden"
                  }
                >
                  <a
                    role="button"
                    className="flex items-center space-y-[3px] px-2 font-normal text-black-300 text-sm capitalize cursor-pointer style-4"
                    onClick={(e) => handleSubChildCategoryBack()}
                  >
                    <img
                      src="img/arrow-right-gray.svg"
                      alt="arrow-right-gray"
                    />{" "}
                    {t("forgotpassword.Back")}
                  </a>
                  <h3 className="px-2 font-normal text-[#2874F0] text-sm">
                    {subChildCategoryList?.categoryNameTrans
                      ? subChildCategoryList?.categoryNameTrans
                      : subChildCategoryList?.name}
                  </h3>
                  <div className="flex flex-col items-start w-full px-4">
                    {subChildCategoryList?.children?.map(
                      (subchildCategory, index) => (
                        <a
                          role="button"
                          key={index}
                          onClick={(e) =>
                            handleSubChildCategoryClick(subchildCategory)
                          }
                          className={`relative w-full font-normal text-black-300 text-sm cursor-pointer style-5 sub ${
                            subChildCategoryslug ==
                              subchildCategory?.categorySlug && "bg-gray-200"
                          }`}
                        >
                          {subchildCategory?.categoryNameTrans
                            ? subchildCategory?.categoryNameTrans
                            : subchildCategory?.name}
                        </a>
                      )
                    )}
                  </div>
                </div>
              </AccordionContent>
            </AccordionPanel>
          </Accordion>
          <Accordion className="flex flex-col mb-4 bg-white border-0 group drop-shadow">
            <AccordionPanel>
              <AccordionTitle className="px-4 py-3 mb-0 text-sm font-medium bg-white border-0 border-b-0 rounded-md hover:bg-white focus:ring-0 text-black-300">
                {t("productcompare.Price")}
              </AccordionTitle>
              <AccordionContent className="px-5 py-5 pt-1 bg-white border-0 border-t-0 rounded-b-lg border-x-0">
                <div className="block overflow-y-auto max-h-36">
                  <div className="relative flex items-start pb-1 gap-x-2">
                    <div className="flex items-center h-6 shrink-0">
                      <input
                        id="radio1"
                        checked={anyPrice}
                        name="radio1"
                        type="radio"
                        className="w-4 h-4 text-black border-gray-300 rounded-full cursor-pointer focus:ring-0 focus:ring-transparent radio-white"
                        onClick={(e) => handleprice(e, "radio1")}
                      />
                    </div>
                    <div className="top-[2px] relative w-full text-sm leading-6">
                      <label
                        htmlFor={"radio1"}
                        onClick={(e) => handleprice(e, "radio1")}
                        className="flex items-center justify-between text-sm font-normal cursor-pointer text-black-300"
                      >
                        {t("priceFilter.anyPrice")}
                      </label>
                    </div>
                  </div>
                  <div className="relative flex items-start pb-1 gap-x-2">
                    <div className="flex items-center h-6 shrink-0">
                      <input
                        id="radio2"
                        checked={customPrice}
                        name="radio2"
                        type="radio"
                        className="w-4 h-4 text-black border-gray-300 rounded-full cursor-pointer focus:ring-0 focus:ring-transparent radio-white"
                        onClick={(e) => handleprice(e, "radio2")}
                      />
                    </div>
                    <div className="top-[2px] relative flex flex-col space-x-[5px]">
                      <div className="w-full text-sm leading-6">
                        <label
                          htmlFor={"radio2"}
                          onClick={(e) => handleprice(e, "radio2")}
                          className="flex items-center justify-between text-sm font-normal cursor-pointer text-black-300"
                        >
                          {t("priceFilter.custom")}
                        </label>
                      </div>
                      <div className="flex items-center space-x-2.5">
                        <input
                          onChange={(e) => handleCustomLowInput(e)}
                          onMouseDown={() => hadleKeyInput()}
                          value={customLowPrice}
                          type="number"
                          onWheel={(e) => e.target.blur()}
                          className="rtl:ml-2.5 p-2.5 border border-[#CCCCCC] focus:border-[#CCCCCC] rounded focus:ring-0 w-full h-[37px] font-normal text-black-300 text-sm"
                          placeholder={t("priceFilter.low")}
                        />
                        <p className="font-normal text-[#BCBCBC] text-sm">
                          {t("priceFilter.to")}
                        </p>
                        <input
                          onChange={(e) => handleCustomHighInput(e, "high")}
                          onMouseDown={() => hadleKeyInput()}
                          value={customHighPrice}
                          type="number"
                          onWheel={(e) => e.target.blur()}
                          className="p-2.5 border border-[#CCCCCC] focus:border-[#CCCCCC] rounded focus:ring-0 w-full h-[37px] font-normal text-black-300 text-sm"
                          placeholder={t("priceFilter.high")}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </AccordionContent>
            </AccordionPanel>
          </Accordion>
        </div>
      </div>
    </div>
  );
}
