"use client";
//React and Next
import { useEffect, useState } from "react";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { offsetCount } from "@/redux/slices/userSlice";
//API
import {
  CategoryParentListApi,
  SearchProductListApi,
  SearchProductListCountApi,
  SubCategoryListApi,
  customfiltersMaxCount,
} from "@/api/clientsideaction/productsearch";
//Components
import Contactus from "../Contactus";
import ProductList from "./ProductList";
import SearchProductFilter from "./Filter";

export default function SearchProductList({ token, searchParams }) {
  const dispatch = useDispatch();
  const search = searchParams?.keyword;
  const offset = useSelector((state) => state.userReducer.offsetVal);
  const [productdetails, setProductdetails] = useState([]);
  const [categoryList, setCategoryList] = useState([]);
  const [categoryslug, setCategoryslug] = useState("");
  const [childCategoryList, setChildCategoryList] = useState([]);
  const [childCategoryShow, setChildCategoryShow] = useState(false);
  const [childCategoryslug, setChildCategoryslug] = useState("");
  const [subChildCategoryList, setSubChildCategoryList] = useState([]);
  const [subChildCategoryShow, setSubChildCategoryShow] = useState(false);
  const [subChildCategoryslug, setSubChildCategoryslug] = useState("");
  const [loader, setLoader] = useState(false);

  // const off = searchParams.get('offset') ? searchParams.get('offset') : 0
  // const cleanedStr = off && off.replace(/[a-zA-Z]+/g, '') ? off.replace(/[a-zA-Z]+/g, '') : 0;
  // const [offset, setOffset] = useState(parseInt(cleanedStr))
  const [currentPage, setCurrentPage] = useState(1);
  const [trigger, setTriiger] = useState(0);
  const [sortname, setSortname] = useState("All");
  const [order, setorder] = useState();
  const [triggers, setTriggers] = useState(0);
  const [mPrices, setMprice] = useState("");
  const [anyPrice, setAnyPrice] = useState(true);
  const [customPrice, setCustomPrice] = useState(false);
  const [customLowPrice, setCustomLowPrice] = useState("");
  const [customHighPrice, setCustomHighPrice] = useState("");
  // Pagination Total Count
  const [productCount, setProductCount] = useState();

  useEffect(() => {
    customfiltersMaxCount(setMprice);
    SearchProductListApi(
      subChildCategoryslug
        ? subChildCategoryslug
        : childCategoryslug
        ? childCategoryslug
        : categoryslug,
      search,
      setProductdetails,
      setLoader,
      offset,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
    SearchProductListCountApi(
      subChildCategoryslug
        ? subChildCategoryslug
        : childCategoryslug
        ? childCategoryslug
        : categoryslug,
      search,
      setProductCount,
      offset,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
    CategoryParentListApi(setCategoryList);
  }, [search]);

  const onPageChange = (data) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth", // smooth scrolling animation
    });
    setTriiger(trigger + 1);
    setLoader(false);
    let limit = 10;
    let offset = Math.ceil((data - 1) * limit);
    dispatch(offsetCount(offset));
    // setOffset(offset)
    setCurrentPage(data);
    // router.push(`/product?keyword=${search}`)
    SearchProductListApi(
      subChildCategoryslug
        ? subChildCategoryslug
        : childCategoryslug
        ? childCategoryslug
        : categoryslug,
      search,
      setProductdetails,
      setLoader,
      offset,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
    SearchProductListCountApi(
      subChildCategoryslug
        ? subChildCategoryslug
        : childCategoryslug
        ? childCategoryslug
        : categoryslug,
      search,
      setProductCount,
      offset,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
  };
  const nPages1 = Math.ceil(productCount != undefined && productCount / 10);
  useEffect(() => {
    setCurrentPage(offset / 10 + 1);
  }, [offset]);

  let obj = {};

  const handleSort = (data) => {
    setLoader(false);
    setSortname(data?.name);
    setTriggers(triggers + 1);
    if (data?.id == 1) {
      obj.price = "ASC";
      obj.latestArrival = 0;
    }
    if (data?.id == 2) {
      obj.price = "DESC";
      obj.latestArrival = 0;
    }
    if (data?.id == 3) {
      obj.price = "";
      obj.latestArrival = 1;
    }
    setorder(obj);
  };

  useEffect(() => {
    let timer = setTimeout(() => {
      SearchProductListApi(
        subChildCategoryslug
          ? subChildCategoryslug
          : childCategoryslug
          ? childCategoryslug
          : categoryslug,
        search,
        setProductdetails,
        setLoader,
        offset,
        order,
        customHighPrice == "" ? mPrices : customHighPrice,
        customLowPrice
      );
      SearchProductListCountApi(
        subChildCategoryslug
          ? subChildCategoryslug
          : childCategoryslug
          ? childCategoryslug
          : categoryslug,
        search,
        setProductCount,
        offset,
        order,
        customHighPrice == "" ? mPrices : customHighPrice,
        customLowPrice
      );
    }, 500);
    return () => {
      clearTimeout(timer);
    };
  }, [triggers, customLowPrice, customHighPrice]);

  const handleParentCategoryClick = (category) => {
    dispatch(offsetCount(0));
    // setOffset(0)
    setLoader(false);
    setCategoryslug(category?.categorySlug);
    setChildCategoryShow(false);
    SubCategoryListApi(
      setChildCategoryList,
      category?.categorySlug,
      setChildCategoryShow
    );
    SearchProductListApi(
      category?.categorySlug,
      search,
      setProductdetails,
      setLoader,
      0,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
    SearchProductListCountApi(
      category?.categorySlug,
      search,
      setProductCount,
      0,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
  };

  const handleChildCategoryBack = () => {
    dispatch(offsetCount(0));
    // setOffset(0)
    setLoader(false);
    setCategoryslug("");
    setChildCategoryShow(false);
    setTriggers(triggers + 1);
  };

  const handleChildCategoryClick = (category) => {
    dispatch(offsetCount(0));
    // setOffset(0)
    setLoader(false);
    setChildCategoryslug(category?.categorySlug);
    setSubChildCategoryList(category);
    setSubChildCategoryShow(true);
    SearchProductListApi(
      category?.categorySlug,
      search,
      setProductdetails,
      setLoader,
      0,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
    SearchProductListCountApi(
      category?.categorySlug,
      search,
      setProductCount,
      0,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
  };

  const handleSubChildCategoryBack = () => {
    dispatch(offsetCount(0));
    // setOffset(0)
    setLoader(false);
    setChildCategoryslug("");
    setSubChildCategoryShow(false);
    setTriggers(triggers + 1);
  };
  const handleResetAll = () => {
    dispatch(offsetCount(0));
    // setOffset(0)
    setLoader(false);
    setChildCategoryslug("");
    setSubChildCategoryShow(false);
    setCategoryslug("");
    setChildCategoryShow(false);
    setAnyPrice(true);
    setCustomPrice(false);
    setCustomLowPrice("");
    setCustomHighPrice("");
    setTriggers(triggers + 1);
  };

  const handleSubChildCategoryClick = (category) => {
    dispatch(offsetCount(0));
    // setOffset(0)
    setLoader(false);
    setSubChildCategoryslug(category?.categorySlug);
    SearchProductListApi(
      category?.categorySlug,
      search,
      setProductdetails,
      setLoader,
      0,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
    SearchProductListCountApi(
      category?.categorySlug,
      search,
      setProductCount,
      0,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
  };

  return (
    <>
      <div className="mx-auto max-w-screen-2xl">
        <div className="flex flex-row gap-4 pb-6 mx-auto">
          <SearchProductFilter
            categoryList={categoryList}
            mPrices={mPrices}
            anyPrice={anyPrice}
            setAnyPrice={setAnyPrice}
            customPrice={customPrice}
            setCustomPrice={setCustomPrice}
            customLowPrice={customLowPrice}
            setCustomLowPrice={setCustomLowPrice}
            customHighPrice={customHighPrice}
            setCustomHighPrice={setCustomHighPrice}
            // setOffset={setOffset}
            childCategoryShow={childCategoryShow}
            handleParentCategoryClick={handleParentCategoryClick}
            childCategoryList={childCategoryList}
            handleChildCategoryBack={handleChildCategoryBack}
            subChildCategoryList={subChildCategoryList}
            subChildCategoryShow={subChildCategoryShow}
            handleChildCategoryClick={handleChildCategoryClick}
            handleSubChildCategoryBack={handleSubChildCategoryBack}
            handleSubChildCategoryClick={handleSubChildCategoryClick}
            subChildCategoryslug={subChildCategoryslug}
            handleResetAll={handleResetAll}
          />
          <ProductList
            productdetails={productdetails}
            loader={loader}
            search={search}
            pagecount={productCount}
            nPages1={nPages1}
            currentPage={currentPage}
            onPageChange={onPageChange}
            token={token}
          />
        </div>
      </div>

      <Contactus token={token} />
    </>
  );
}
