"use client";

// React & Next Imports
import Link from "next/link";
import { Fragment, useState } from "react";
import { useTranslation } from "react-i18next";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";
//Redux
import { useDispatch, useSelector } from "react-redux";

//Api
import { imageUrl } from "@/utilities/url";
import ToastMessage from "@/Components/ToastMessage";
import { GetCompareId } from "@/utilities/localStorage";
import {
  AddWishListApi,
  WishListDeleteApi,
  productCompareApi,
} from "@/api/clientsideaction/productsearch";

//Utils
import PaginationComp from "@/Components/Pagination";

//Componets
import CustomCurrencyFormatter from "../CustomCurrencyFormatter";

export default function ProductList({
  productdetails,
  loader,
  search,
  pagecount,
  nPages1,
  currentPage,
  onPageChange,
  token,
}) {
  const { t } = useTranslation();
  const router = useRouter();
  
  const skele = [1, 2, 3, 4];
  const wishCount = useSelector((state) => state.userReducer.wishCount);
  const [comparetrigger, setCompareTrigger] = useState(0);
  const [trigger, setTrigger] = useState(0);

  const dispatch = useDispatch();
  const productIds = GetCompareId();
  const handleBuyNow = (datacart) => {
    router.push(`/products/${datacart?.productSlug}`);
  };

  const handleWishiList = (data) => {
    if (token) {
      productdetails?.map((product) => {
        if (data.productId == product.productId) {
          product.wishListStatus = product.wishListStatus == 0 ? 1 : 0;
        }
      });

      if (data?.wishListStatus == 0) {
        WishListDeleteApi(data.productId, dispatch, wishCount);
      } else {
        AddWishListApi(data.productId, dispatch, wishCount);
      }
    } else {
      router.push("/buyer-login");
    }
    setTrigger(trigger + 1);
  };

  const handleAddItemToCompare = (e, productId) => {
    let data = 0;
    if (productIds !== null && productIds !== undefined) {
      var productIdIndex = productIds?.indexOf(productId);
      if (productIdIndex !== -1) {
        let localCompareId = GetCompareId();
        localCompareId.splice(productIdIndex, 1);
        localStorage.setItem("compareId", JSON.stringify(localCompareId));
        setCompareTrigger(comparetrigger + 1);
        ToastMessage({
          type: "success",
          message: t("toastMessage.compareProductRemove"),
        });
      } else {
        const compareIds = [...productIds, productId];
        if (compareIds && compareIds.length > 3) {
          ToastMessage({
            type: "error",
            message: t("toastMessage.compareProductLimit"),
          });
        } else {
          productCompareApi(
            data,
            compareIds,
            setCompareTrigger,
            comparetrigger,
            productIds
          );
        }
      }
    } else {
      let productIds = [];
      productIds.push(productId);
      productCompareApi(data, productIds, setCompareTrigger, comparetrigger);
    }
  };

  function compareCheckFunction(productId) {
    let productIds = JSON.parse(localStorage.getItem("compareId"));

    if (productIds && productIds.length !== 0) {
      let compareCheck = productIds.some((value) => value === productId);
      return compareCheck;
    }
  }

  return (
    <div className="px-4 pt-2.5 sm:pl-0 w-full">
      <div className="flex items-center justify-between pb-3 mb-4 border-b border-slate-300">
        <h3 className="text-lg font-black text-black-300">
          <span className="font-normal font-sm text-black-300">
            {t("myprofile.resultsfor")}
          </span>{" "}
          {search}{" "}
          <span className="font-normal font-sm text-black-900">
            ({pagecount && pagecount ? pagecount : 0})
          </span>
        </h3>
      </div>
      {skele.map(
        (_, index) =>
          loader == false && (
            <Fragment key={index}>
              <div
                role="status"
                className="space-y-8 md:flex md:items-center rtl:space-x-reverse md:space-x-8 md:space-y-0 animate-pulse"
              >
                <div className="flex items-center justify-center w-full h-48 bg-gray-300 rounded dark:bg-gray-700 sm:w-96">
                  <svg
                    className="w-10 h-10 text-gray-200 dark:text-gray-600"
                    aria-hidden="true"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="currentColor"
                    viewBox="0 0 20 18"
                  >
                    <path d="M18 0H2a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2Zm-5.5 4a1.5 1.5 0 1 1 0 3 1.5 1.5 0 0 1 0-3Zm4.376 10.481A1 1 0 0 1 16 15H4a1 1 0 0 1-.895-1.447l3.5-7A1 1 0 0 1 7.468 6a.965.965 0 0 1 .9.5l2.775 4.757 1.546-1.887a1 1 0 0 1 1.618.1l2.541 4a1 1 0 0 1 .028 1.011Z" />
                  </svg>
                </div>
                <div className="w-full">
                  <div className="bg-gray-200 dark:bg-gray-700 mb-4 rounded-full w-48 h-2.5"></div>
                  <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full max-w-[480px] h-2"></div>
                  <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
                  <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full max-w-[440px] h-2"></div>
                  <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full max-w-[460px] h-2"></div>
                  <div className="bg-gray-200 dark:bg-gray-700 rounded-full max-w-[360px] h-2"></div>
                </div>
              </div>
              <div className="block my-8 border-b border-color"></div>
            </Fragment>
          )
      )}

      {loader == true && productdetails && productdetails?.length != 0 ? (
        productdetails &&
        productdetails?.map((data, index) => (
          <Fragment key={index}>
            {productIds && productIds?.length > 0 && (
              <Link
                prefetch={false}
                href={"/product-compare"}
                className={
                  "inline-flex cursor-pointer justify-center uppercase font-medium items-center rounded-md bg-[var(--color)] text-white text-base gap-x-4 py-4 px-8 fixed bottom-11 right-11 z-10 w-36 h-10"
                }
              >
                {t("compare.Compare")}
                <span className="flex items-center justify-center bg-green-slate min-w-2">
                  {productIds?.length}
                </span>
              </Link>
            )}
            <div
              className="relative flex flex-col p-4 mb-4 space-y-6 border border-white rounded cursor-pointer group group-item sm:flex-row sm:space-x-6 sm:space-y-0 hover:bg-slate-50 card-shadow hover:border hover:border-black"
              key={index}
            >
              <div
                onClick={() => handleWishiList(data)}
                className={
                  data.wishListStatus == 0
                    ? "item cursor-pointer absolute top-2 right-2 bg-white z-20 rounded-full flex justify-center items-center shadow-md h-8 w-8 text-2xl hover:bg-slate-200 tooltip2"
                    : `cursor-pointer absolute top-2 right-2 z-20 bg-white rounded-full flex justify-center items-center shadow-md h-8 w-8 text-2xl hover:bg-slate-200 tooltip2`
                }
              >
                {data.wishListStatus == 0 ? (
                  <img
                    src="/img/heart-outline-wisit.svg"
                    alt="heart-outline-wisit"
                  />
                ) : (
                  <img
                    src="/img/heart-select-red-wisit.svg"
                    alt="heart-select-red-wisit"
                  />
                )}

                <span className="tooltip-text">
                  {t("Breadcrumb.wishList")}
                </span>
              </div>
              <div
                onClick={(e) => handleAddItemToCompare(e, data?.productId)}
                className={
                  compareCheckFunction(data?.productId)
                    ? "cursor-pointer absolute top-12 right-2 z-20 bg-white rounded-full flex justify-center items-center shadow-md h-8 w-8 text-2xl hover:bg-slate-200 tooltip2"
                    : `item cursor-pointer absolute top-12  z-20 right-2 bg-white rounded-full flex justify-center items-center shadow-md h-8 w-8 text-2xl hover:bg-slate-200 tooltip2`
                }
              >
                {compareCheckFunction(data?.productId) ? (
                  <img
                    src="/img/compare-outline-active.svg"
                    alt="compare-outline-active"
                  />
                ) : (
                  <img src="/img/compare-outline.svg" alt="compare-outline" />
                )}
                <span className="tooltip-text">{t("tooltip.compare")}</span>
              </div>

              <div className="flex items-center max-sm:mx-auto w-[182px] h-[182px] shrink-0">
                <img
                  src={
                    imageUrl +
                    "?path=" +
                    data?.containerName +
                    "&name=" +
                    data?.image +
                    "&width=182&height=182"
                  }
                  alt={data?.name}
                  width={182}
                  height={182}
                  className="w-auto h-full max-w-full mx-auto"
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = "/img/no-image.png";
                    currentTarget.alt = "no-image";
                  }}
                />
              </div>
              <div className="flex flex-col w-full grow">
                <h3 className="mb-4 text-base cursor-pointer text-black-300 group-hover:underline">
                  {data?.productNameTrans ? data?.productNameTrans : data?.name}
                </h3>
                <h2 className="mb-2 text-lg font-black leading-none text-black-300">
                  {" "}
                  <CustomCurrencyFormatter
                    price={data?.price ? data?.price : data?.defaultPrice}
                  />
                </h2>
                <Link
                  prefetch={false}
                  href={`/shop/${data?.displayNameUrl}`}
                  className="z-10 mb-2 text-sm underline text-black-900 hover:text-blue-500"
                >
                  {data?.vendorName}
                </Link>
                <div className="relative z-10 flex flex-wrap gap-3 mt-auto sm:gap-6">
                  <button
                    onClick={(e) => handleBuyNow(data)}
                    className="flex justify-center items-center space-x-[5px] bg-boldblack hover:bg-zinc-800 px-4 sm:px-[41px] rounded w-full sm:w-auto h-[49px] font-semibold text-white text-sm whitespace-nowrap btn"
                  >
                    {" "}
                    <img src="/img/shop-cart.svg" alt="shop-cart" />{" "}
                    <p>{t("productlist.Buynow")}</p>
                  </button>
                </div>
              </div>
              <Link
                prefetch={false}
                href={`/products/${data?.productSlug}`}
                className="absolute top-0 left-0 w-full h-full"
              ></Link>
            </div>
          </Fragment>
        ))
      ) : (
        <div className="mx-auto mt-32 min-h-[40vh]">
          <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
            <img src="/img/no-data.svg" alt="no-data" />
            <div>
              <h5 className="mb-1 text-xl font-black text-black-300">
                {t("myprofile.NoResults")}{" "}
                <span className="text-green-500 capitalize">
                  &quot;{search}&quot;
                </span>
              </h5>
            </div>
          </div>
        </div>
      )}

      <div className="relative flex flex-col w-full mt-4 sm:flex-row">
        {pagecount >= 10 && (
          <>
            <div className="absolute left-0 hidden sm:block top-4">
              {t("productlist.pages")} {currentPage} {t("productlist.of")}{" "}
              {nPages1}
            </div>

            <PaginationComp
              nPages1={nPages1}
              currentPage={currentPage}
              onPageChange={onPageChange}
            />
          </>
        )}
      </div>
    </div>
  );
}
